import React from 'react';
import { CFormSwitch } from '@coreui/react'
import { useUserStore } from 'src/userStore';
const ActionButton = (props) => {
    const hasPerm = useUserStore((state) => state.hasPerm)
    let prefix = React.useMemo(() => {
        return props.prefix
    }, [props.prefix])
    return (
        <>
            {
                (props.onChangeStatus) ? <span data-title="Change Status" className={`${!hasPerm(`status_${prefix}`) ? 'disabled' : ""}`}><i className="me-2 fa-solid fa-check-circle text-primary pointer" onClick={props.onChangeStatus}></i></span> : ''
            }
            {
                (props.onView) ? <span data-title="View Data" className={`${!hasPerm(`view_${prefix}`) ? 'disabled' : ""}`}><i className="fa-solid fa-eye pointer me-2 text-info" onClick={props.onView}></i></span> : ''
            }

            {
                (props.onEdit) ? <span data-title="Edit Data" className={`${!hasPerm(`change_${prefix}`) ? 'disabled' : ""}`}><i className="fa-solid fa-pen-to-square pointer me-2 text-warning" onClick={props.onEdit}></i></span> : ''
            }

            <span data-title="Delete Data" className={`mx-3 ${!hasPerm(`delete_${prefix}`) ? 'disabled' : ""}`}><i className=" fa-solid fa-trash pointer text-danger" onClick={props.onDelete}></i></span>
        </>
    )
}

export default ActionButton;