import React from "react";
import DataTable from "react-data-table-component";
import Constants, { userToken } from "src/Constants";
import ActionButton from "./ActionButton";
import { ToastContainer, toast } from 'react-toastify';
import { CCard, CCardBody, CFormInput, CButton } from "@coreui/react";
import { useNavigate } from "react-router-dom";
import ChangeStatusModal from "./ChangeStatusModal";

const SubAdminTable = (props) => {

    const navigate = useNavigate()
    const [data, setData] = React.useState([]);
    const [pagination, setPagination] = React.useState({
        per_page: 100,
        page_number: 1
    })
    const [filter, setFilter] = React.useState({
        search : '',
        status : ''
    })
    const [current, setCurrent] = React.useState(null)
    const [load, setLoad] = React.useState(true)
    const [total, setTotal] = React.useState(0);
    const columns = [
        {
            name: 'Id',
            selector: row => row.id,
            sortable: true,
        },
        {
            name: 'First Name',
            selector: row => row.first_name,
            sortable: true,
        },
        {
            name: 'Last Name',
            selector: row => row.last_name,
            sortable: true,
        },
        {
            name: 'Username',
            selector: row => row.username,
            sortable: true,
        },
        {
            name: 'Email',
            selector: row => row.email,
            sortable: true,
        },
        {
            name: 'Contact Number',
            selector: row => <a className="fw-bold text-dark text-decoration-none" href={`tel:${row.phone}`}><i className="fa-solid fa-phone me-1"></i>{row.phone}</a>,
            sortable: true,
        },
        {
            name: 'Admin Type',
            selector: row => row.type,
            sortable: true,
        },
        {
            name: 'Action',
            selector: (row) => <ActionButton prefix="crossing" onEdit={() => handleEdit(row)} onDelete={() => handleDelete(row.id)} />,
            sortable: true
        }
    ];

    const handleEdit = (row) => {
        // console.log(row);
        navigate('/subadmin/edit', { state: row });
    }
    const handleDelete = async (id) => {
        let confirmation  = window.confirm("Do you really want to delete this record?");
        if(!confirmation) {
            return;
        }
        let url = Constants.subAdminDelete + id;
        let response = await fetch(url, {
            method: 'GET',
            headers: {
                'Authorization': userToken(),

            }
        })
        let json = await response.json();
        if (json.status === 200) {
            setLoad(!load);
            toast.success("Record deleted successfully!");
        }
    }

    const handleSearch = () => {
        let search = document.querySelector('.search-text').value;
        setFilter({search, status: 1})

    }

    React.useEffect(() => {
        let { sessionStorage } = window;
        if( sessionStorage.type && sessionStorage.message) {
            toast[sessionStorage.type](sessionStorage.message)
            sessionStorage.clear()
        }
    }, [])
    React.useEffect(() => {
        let url = Constants.subAdminList;


        (async () => {
            let response = await fetch(url + `?per_page=${pagination.per_page}&page_number=${pagination.page_number}&search=${filter.search}`, {
                method: 'GET',
                headers: {
                    'Authorization': userToken(),

                }
            })
            let data = await response.json();
            // console.log(data);
            setData(data.data);
            setTotal(data.total);


        })()
    }, [pagination, load, filter])


    return (
        <>
            <ToastContainer
                position="top-right"
                theme="colored"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover />
            <CCard className="my-2">
                <CCardBody>
                    <div className="row">
                        <div className="col-5">
                            <CFormInput type="text" placeholder="Search.." className="search-text"/>
                        </div>
                        {/* <div className="col-5">
                            <CFormSelect className="search-status">
                                <option hidden={true} value="">Select</option>
                                <option value="0">Closed</option>
                                <option value="1">Open</option>
                                <option value="2">Caution</option>
                            </CFormSelect>
                        </div> */}
                        <div className="col-2">
                            <CButton type="button" className="float-end w-100" color="primary" onClick={handleSearch}>Search</CButton>
                        </div>
                    </div>
                </CCardBody>

            </CCard>
            <DataTable
                columns={columns}
                data={data}
                pagination
                paginationServer
                paginationTotalRows={total}
                paginationPerPage={100}
                paginationRowsPerPageOptions={[100, 50, 30]}
                onChangeRowsPerPage={(per_page) => setPagination({ ...pagination, per_page })}
                onChangePage={(page_number, per_page) => setPagination({ ...pagination, page_number })}
            />
            <ChangeStatusModal id={current} setCurrent={setCurrent} setLoad={setLoad}/>
        </>
    )
}

export default SubAdminTable;