import React from 'react';
import { CModal, CModalBody, CForm, CFormSelect, CButton, CFormTextarea, CFormLabel } from '@coreui/react';
import Constants, { userToken } from 'src/Constants';
const ChangeStatusModal = ({ id, setCurrent, setLoad }) => {



    const handleSubmit = async () => {
        let status = document.querySelector('#statusInput').value;
        let comment = document.querySelector('#commentInput').value;
        let body = JSON.stringify({ status, comment  })
        let url = Constants.updateCrossingStatus + id;

        let response = await fetch(url, {
            method: 'POST',
            headers: {
                'Authorization': userToken(),
                'Content-Type': 'application/json'

            },
            body
        })
        let data = await response.json();

        if (data.status === 200) {
            setLoad((p) => !p)
            setCurrent(null)
        }

    }
    return (
        <>

            <CModal
                backdrop={false}
                keyboard={false}
                portal={false}
                visible={id ? true : false}
                alignment="center"
            >

                <CModalBody>
                    <h6 className='my-3 custom-header'>
                        Update Status

                        <span onClick={() => setCurrent(null)}>&times;</span>
                    </h6>
                    <CForm>
                        <CFormSelect aria-label="Default select example" id='statusInput'>
                            <option value="1">Open</option>
                            <option value="0">Close</option>
                            <option value="2">Caution</option>
                            <option value="3">Long time closure</option>
                        </CFormSelect>
                        <CFormLabel htmlFor="address">Comments</CFormLabel>
                        <CFormTextarea id="commentInput" rows="3" name="comment"></CFormTextarea>
                        <CButton type='button' className='my-3 px-3' onClick={handleSubmit}>Save</CButton>

                    </CForm>

                </CModalBody>

            </CModal>

        </>
    )
}

export default ChangeStatusModal;